html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Dancing Script", sans-serif;
  font-size: 18px;
  font-weight: 400;
 
}

